.App {
  text-align: center;
}
.App-logo {
  -webkit-animation: App-logo-spin 20s linear infinite;
  animation: App-logo-spin 20s linear infinite;
  height: 40vmin;
  pointer-events: none;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column;
  -webkit-align-items: center;
  align-items: center;
  -webkit-justify-content: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #fff;
}
.App-link {
  color: #61dafb;
}
@-webkit-keyframes App-logo-spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(1turn);
    transform: rotate(1turn);
  }
}
@keyframes App-logo-spin {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(1turn);
    transform: rotate(1turn);
  }
}
.bigred {
  font-family: Courier New;
  font-style: italic;
  font-size: xx-large;
  color: red;
}
.react-autosuggest__input {
  width: 240px;
  height: 30px;
  padding: 0 0 0 5px;
  font-family: Helvetica, sans-serif;
  font-weight: 300;
  font-size: 16px;
  border: none;
  border-bottom: 2px solid #0d47a1;
  margin-bottom: 2vh;
}
.react-autosuggest__input:focus {
  outline: none;
}
.lamp {
  height: 3rem;
  width: 3rem;
  border-style: solid;
  border-width: 2px;
  border-radius: 50%;
}
.light {
  border: solid;
  width: 4.25rem;
}
.flex-container {
  -webkit-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-justify-content: space-evenly;
  justify-content: space-evenly;
}
.flex-container,
.scenario-container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
}
.scenario-container {
  -webkit-flex-direction: column;
  flex-direction: column;
  margin-top: 2em;
}
.scenario-message {
  color: #fff;
  width: 80%;
  margin: 2px;
  padding: 1em;
  font-size: smaller;
  font-weight: 700;
}
.usersList {
  margin-top: 2px;
  margin-bottom: 2px;
  box-sizing: border-box;
  height: calc(80vh - 70px);
  overflow-x: hidden;
}
.userBubble {
  font-family: Arial, Helvetica, sans-serif;
  position: relative;
  padding: 5px;
  width: 20%;
  margin-bottom: -1em;
  border-radius: 10px;
  left: 5%;
  color: #fff;
}
.iClass {
  float: right;
  width: 90%;
}
.iClass:nth-child(3n) .userBubble {
  background: #095bd6;
}
.Triangle {
  content: "";
  position: absolute;
  bottom: -15px;
  left: 140px;
  border-width: 10px 10px 0;
  border-style: solid;
}
.iClass .Triangle {
  left: 320px;
}
@media only screen and (max-width: 414px) {
  .usersList {
    margin-top: 2px;
    margin-bottom: 2px;
    box-sizing: border-box;
    height: calc(80vh - 70px);
    overflow-x: hidden;
  }
  .userBubble {
    font-family: Arial, Helvetica, sans-serif;
    position: relative;
    padding: 5px;
    width: 70%;
    margin-bottom: -1em;
    border-radius: 10px;
    left: 5%;
    color: #fff;
  }
  .iClass {
    float: right;
    width: 90%;
  }

  .Triangle {
    content: "";
    position: absolute;
    bottom: -13px;
    left: 50px;
    border-width: 10px 10px 0;
    border-style: solid;
  }
  .iClass .Triangle {
    left: 200px;
  }
}

